<template>
<div>
    <!--农场-->
    <div class="panel panel-info">
        <div class="panel-body">
            <div>实时监测</div>
            <div>
                <div class="panel panel-default">
                    <div class="panel-heading">
                        <div>
                            <h4>
                                <span v-for="(item,index) in typeCheckData" :key="index" v-html="item.name+'&nbsp;&nbsp;&gt;&nbsp;&nbsp;'" v-show="index<typenum"></span>
                                <span v-html="initData[typenum].title"></span>
                                <a v-if="typenum!=0" class="btn btn-link pull-right" @click="loginItems(-1)">返回上一级</a>
                            </h4>
                        </div>
                    </div>
                    <div class="panel-body">
                        <!--选择位置-->
                        <div v-show="!sensorInfVisible">
                            <div v-if="items.length>0">
                                <div class="col-sm-2 col-md-1 col-lg-1" v-for="(item,index) in items" :key="index">
                                    <button class="btn btn-default" @click="loginItems(1,item)"  v-html="item.name"></button>
                                </div>
                            </div>
                            <div v-show="items.length<=0" class="col-sm-12 txt-center cursor-pointer logindata-error">
                                未查找到数据，点击请重试
                            </div>
                        </div>
                        <!--传感器详细信息-->
                        <div v-show="sensorInfVisible">
                            <div class="pull-right">
                                <a class="btn btn-link"  @click="watchDraw(0)" v-show="NowAndHistory!=0">查看当前数据</a>
                                <a class="btn btn-link"  @click="watchDraw(1)" v-show="NowAndHistory!=1">查看历史记录</a>
                                <a class="btn btn-link"  @click="watchDraw(2)" v-show="NowAndHistory!=2">查看数据预测</a>
                            </div>
                            <!--当前信息-->
                            <div v-show="NowAndHistory==0">
                                <div id="myChartNow" :style="{width: '600px', height: '500px',margin:'auto'}"></div>
                            </div>
                            <!--历史记录-->
                            <div v-show="NowAndHistory==1||NowAndHistory==2">
                                <div id="myChartHistory" :style="{width: '800px', height: '500px',margin:'auto'}"></div>
                            </div>
                            <!-- <div v-show="NowAndHistory==2">
                                <div id="myChartDataPrediction" :style="{width: '800px', height: '500px',margin:'auto'}"></div>
                            </div> -->
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import chartFun from '@/echarts/index'
export default {
    data() {
        return {
            sensorInfVisible: false, //是否显示传感器图表数据
            NowAndHistory: 0, //当前数据，历史记录，数据预测切换
            typenum: 0,
            items: [{
                id: 1, // 设备 农场 id
                name: 1 // 设备 等 名称
            }],
            sensorInf: {
                max: 35, // 当前节点种植作物环境上限
                min: 26, // 当前节点种植作物环境下限
                now: 24, // 当前节点当前温度
                type: "温度|气压" //传感器类型
            },
            typeCheckData: [],
            initData: [{
                    url: "Sensor/selectAddress.action",
                    title: "农场"
                },
                {
                    url: "Sensor/selectGreenHouse.action",
                    title: "大棚"
                },
                {
                    url: "Sensor/selectGreenHouseEquipment.action",
                    title: "设备"
                },
                {
                    url: "Sensor/selectSensor.action",
                    title: "传感器",

                },
            ],
        }
    },
    mounted() {
        this.loginThisItems(this.initData[0].url)
    },
    methods: {
        //判断层次改变
        loginItems(direction, item = "") {
            var vm = this;
            var typenum = vm.typenum;
            if (item != "") {
                vm.typeCheckData[vm.typenum] = item;
            }
            if (typenum == vm.initData.length - 1) {
                if (direction > 0) {
                    vm.NowAndHistory = 0;
                    vm.drawChartNow();
                } else if (direction < 0 && vm.sensorInfVisible) {
                    vm.sensorInfVisible = false;
                    vm.NowAndHistory = true;
                    return;
                }
            }
            if (direction > 0) {
                vm.typenum += 1;
            } else{
                vm.typenum -= 1;
            }
            typenum = vm.typenum;
            if (typenum <= vm.initData.length - 1) {
                vm.loginThisItems(vm.initData[typenum].url, item != '' ? item.id : (vm.typenum == 0 ? "" : vm.typeCheckData[typenum].id))
            } else {
                vm.typenum = vm.initData.length - 1;
                typenum = vm.typenum;
            }
        },
        //层次改变的请求
        loginThisItems(url, id = "") {
            var vm = this;
            vm.$axios.post(vm.HOST + url, id !={}? {
                id: id
            } : "").then(success => {
                vm.items = success.data;
            }).catch(error => {})
        },
        //监听需要绘制的界面
        watchDraw(index) {
            var vm = this;
            if (index == 0) {
                vm.drawChartNow();
            } else if (index == 1) {
                vm.drawChartHistory();
            } else {
                vm.drawDataPrediction();
            }
            vm.NowAndHistory = index;
        },
        //绘制当前信息
        drawChartNow() {
            var vm = this;
            let myChart = vm.$echarts.init(document.getElementById('myChartNow'));
            var splitNumber = 10;
            var unittype = vm.drawChartSensorType(0);
            var min = vm.sensorInf.min;
            var max = vm.sensorInf.max;
            var len = max - min;
            if (len > 10 || len < 6) {
                if (len > 10 && len < 20) {
                    splitNumber = 10;
                }
            } else if (len <= 10 && len >= 6) {
                splitNumber = (len) + 4;
            }
            var option = chartFun.sensorInfNow(unittype, splitNumber, min, max, len);
            myChart.setOption(option, true);
            ResSensorInf();
            var interval=setInterval(function () {
                console.log(vm.NowAndHistory+":"+vm.typenum)
                if(vm.NowAndHistory==0&&vm.typenum==vm.initData.length - 1){
                ResSensorInf();
                }else{
                    clearInterval(interval);
                }
            }, 1000);
            vm.sensorInfVisible = true;
            function ResSensorInf() {
                vm.$axios.post(vm.HOST + "collect/selectLatest.action", {
                    sensor_id: vm.typeCheckData[vm.typenum].id
                }).then(success => {
                    option.series[0].data[0].value = success.data.sensorValue;
                    myChart.setOption(option, true);
                }).catch(error => {})
            }
        },
        //绘制历史记录
        drawChartHistory() {
            var vm = this;
            let myChart = vm.$echarts.init(document.getElementById('myChartHistory'));
            var unittype = vm.drawChartSensorType(0);
            vm.$axios.post(vm.HOST + "collect/selectQuarter.action", {
                sensor_id: vm.typeCheckData[vm.typenum].id
            }).then(success => {
                var resdata = success.data.list;
                var xdata = [];
                var data = [];
                resdata.forEach(item => {
                    xdata.push(vm.$api.opreate.DateCompletion(item.collectTime))
                    data.push(item.sensorValue)
                });
                var option = chartFun.sensorInfHis(unittype, xdata, data);
                myChart.setOption(option, true);
            }).catch(error => {})
        },
        //绘制数据预测
        drawDataPrediction() {
            var vm = this;
            let myChart = vm.$echarts.init(document.getElementById('myChartHistory'));
            var unittype = vm.drawChartSensorType(0);
            vm.$axios.post(vm.HOST + "predict/predictOneDay.action", {
                sensor_id: vm.typeCheckData[vm.typenum].id
            }).then(success => {
                console.log(success)
                var resdata = success.data.list;
                var xdata = [];
                var data = [];
                resdata.forEach(item => {
                    xdata.push(vm.$api.opreate.DateCompletion(item.collectTime))
                    data.push(item.sensorValue)
                });
                var option = chartFun.sensorInfHis(unittype, xdata, data,"数据预测曲线图");
                myChart.setOption(option, true);
            }).catch(error => {})
            
        },
        //传感器类型对应单位和名称
        drawChartSensorType(i) {
            var data = [{
                unit: "℃",
                name: "温度"
            }, {
                unit: "Pa",
                name: "气压"
            }, {
                unit: " %RH",
                name: "湿度"
            }];
            return data[i];
        }
    }
}
</script>

<style scoped>
.logindata-error {
    font-size: 18px;
    color: rgb(243, 29, 29);
}
</style>



